nav {
    background-color: rgba(45, 45, 45, 0.1);
    color: #ffffff;
    width: 13%;
    position: sticky;
    margin: 0 5px;
}
ul{
    background-color: transparent; 
}
nav ul li{
    background-color: transparent; 
    
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}
li:first-child {
    border: none;
}
button {
    color: rgba(255, 255, 255, 0.85);
    font-family: Verdana, sans-serif;
    font-weight: 100;
    background-color: transparent;
    border: none;
    font-size: 18px;
    padding: 5px 10px;
    text-align: left;
    cursor: pointer;
    display: block;
    width: 100%;
}
@media screen and (max-width: 1000px) {
    nav {
        width: 14%;
    }
}
@media screen and (max-width: 800px) {
    nav {
        min-width: 110px;
    }
}